<template>
  <div class="wrapper">
    <div class="content" v-for="item in shop" :key="item.id">
      <listItem :shop="item"></listItem>
    </div>
  </div>
</template>

<script>
import listItem from "./list-item";
export default {
  name: 'list-wrapper',
  components: {
    listItem
  },
  props: {
    id: {
      type: String,
      default: () => {
        return ''
      }
    },
    latitude: {
      type: String,
      default: () => {
        return ''
      }
    },
    longitude: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      shop: []
    }
  },
  async mounted () {
    try {
      let resShop = await this.$api.WMC.getShopsDatas({
        term_id: '6',
        latitude: this.latitude,
        longitude: this.longitude
      })
      if (resShop.data.code === 0) {
        this.shop = resShop.data.data
      }
      console.log(this.shop);
      
    } catch (error) {
      this.$Message.error({
        content: error,
        duration: 4,
        closable: true
      });
    }
  }
}
</script>

<style lang="stylus" scoped>
.wrapper {
  margin-top: 40px;

  .item-right {
    div {
      display: flex;
      align-items: center;
      margin-right: 3%;

      p {
        font-size: 40px;
        color: #fff;
      }

      .address {
        width: 80px;
        height: 80px;
      }
    }
  }

  .content {
    margin: 40px 0 20px;
  }
}
</style>
